body {
  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 18px;
  color: black;
  background-color: white;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  width: 100%;
  background-color: black;
}
.header__wrapper {
  width: 1000px;
  margin: 0 auto;
  padding: 23px 10px 19px;
  background-color: black;
}
.header__logo {
  width: 227px;
  color: white;
  background-color: black;
}
.main {
  width: 100%;
}
.offers {
  width: 100%;
}
.offers__wrapper {
  display: flex;
  justify-content: space-between;
  width: 1000px;
  margin: 0 auto;
  padding: 20px 0;
  padding-right: 10px;
  padding-bottom: 32px;
}
.offers__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.offers__item {
  display: flex;
  width: 710px;
  padding: 0;
  padding-top: 18px;
  padding-bottom: 16px;
}
.offers__item:not(:last-child) {
  border-bottom: 1px solid #d7d7d7;
}
.offers__image {
  width: 138px;
}
.offers__text {
  display: flex;
  flex-direction: column;
  width: 370px;
  padding: 0;
  padding-left: 20px;
}
.offers__title {
  margin: 0;
  padding: 14px 0;
  font-size: 18px;
}
.offers__description {
  padding: 0;
  padding-right: 30px;
  margin: 0;
}
.offers__offer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 202px;
  padding-top: 32px;
  font-size: 24px;
  font-weight: bold;
}
.offers__offer--high {
  padding: 0;
  padding-bottom: 28px;
}
.offers__price-container {
  margin-bottom: 20px;
  text-align: center;
}
.offers__button-add-basket {
  padding: 8px 0;
  font-family: "Arial", sans-serif;
  text-align: center;
  font-size: 20px;
  color: white;
  text-shadow: 0 2px 2px rgba(2, 89, 117, 0.6);
  background-color: #00ade4;
  border: none;
  border-radius: 5px;
  transition-duration: 0.25s;
  cursor: pointer;
}
.offers__button-add-basket:hover,
.offers__button-add-basket:focus {
  background-color: #29d807;
}
.offers__button-add-basket:active {
  opacity: 0.7;
}
.basket {
  position: sticky;
  top: 0;
  width: 227px;
  display: flex;
  flex-direction: column;
  align-self: flex-start;
  padding: 15px;
  margin-top: 35px;
  font-weight: bold;
  border: 1px solid lightgrey;
  border-radius: 10px;
}
.basket__title {
  position: relative;
  margin: 0;
  margin-bottom: 25px;
  padding: 24px 0;
  padding-left: 108px;
  font-size: 18px;
  line-height: 18px;
}
.basket__title::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 91px;
  height: 63px;
  background: url("../img/basket.png") no-repeat 0 0;
}
.basket__list {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 200px;
  padding-bottom: 18px;
  border-bottom: 1px solid #d7d7d7;
}
.basket__item {
  display: flex;
  justify-content: space-between;
  width: 200px;
  margin-bottom: 12px;
}
.basket__item--empty {
  display: inline-block;
  text-align: center;
}
.basket__item--hidden {
  display: none;
}
.basket__button-remove {
  display: inline-block;
  width: 9px;
  height: 9px;
  vertical-align: top;
  margin-right: 3px;
}
.basket__button-remove:hover .basket__button-icon,
.basket__button-remove:focus .basket__button-icon {
  fill: red;
}
.basket__button-remove:active .basket__button-icon {
  fill: darkred;
}
.basket__button-icon {
  width: 9px;
  height: 9px;
  fill: grey;
  cursor: pointer;
}
.basket__offer-title {
  display: inline-block;
  width: 105px;
  line-height: 15px;
}
.basket__total {
  width: 200px;
  margin: 0;
  margin-top: 10px;
  margin-bottom: 28px;
  text-align: right;
}
.basket__button {
  padding: 8px 0;
  font-family: "Arial", sans-serif;
  text-align: center;
  font-size: 18px;
  color: white;
  text-shadow: 0 2px 2px rgba(2, 89, 117, 0.6);
  background-color: #29d807;
  border: none;
  border-radius: 5px;
  transition-duration: 0.25s;
  cursor: pointer;
  padding: 10px;
}
.basket__button:hover,
.basket__button:focus {
  background-color: #00ab1d;
}
.basket__button:active {
  opacity: 0.7;
}
.footer {
  width: 100%;
  background-color: #10546b;
}
.footer__wrapper {
  width: 1000px;
  padding-bottom: 125px;
  background-color: #10546b;
}
